body,html{
  background-image:linear-gradient(to right, rgba(0, 0, 0, 0.7) 0 100%),url("https://getwallpapers.com/wallpaper/full/2/1/b/1503586-free-desktop-wallpaper-travel-1920x1080-for-xiaomi.jpg");
  background-size: cover;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-weight: 1000;
  height: 100%;
  background-attachment: fixed;
  font-size: large;
  color: #ffffff;
}
.App {
  text-align: center;
}

header {
  color: #fff;
  padding: 20px 10px;
}

section{
  height: 100dvh;
}

nav ul {
  list-style: none;
  float: right;
  padding: 0;
}

nav ul li {
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size:20px;
  position: relative;
  display: inline-block;
  margin-right: 25px;
  padding-left: 20px ;
  padding-right: 20px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  transform: translate(0px,0px);
}
nav ul li:hover {
  transform:translate(1%,-5%);
}

nav ul li a::after{
  content: '';
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 0;
  height:2px;
  width: 100%;
  transform-origin: bottom right;
  transform: scaleX(0);
  background-color: orangered;
  transition: transform 300ms ease-in-out;
}
nav ul li a:hover::after{
  transform: scaleX(1);
  transform-origin: bottom left;
}

section#destinations {
  padding: 50px 0;
}

.destination-list {
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
  float:left ;
  transform: translateY(-25%);
  height: 600px;
  width: 500px;
}

.destination-card {
  margin: 20px;
  padding: 20px;
  height: 500px;
  width:500px;
  transform: translate(-10%);
  color:white;
}

#one,#two,#three,#four{
  position: relative;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5) 0 100%),url("https://cdn1.matadornetwork.com/blogs/1/2017/11/Na-Pali-Coast-Kauai-Hawaii-aerial-photo.jpg");
  background-position: 60%;
  background-size: 250%;
  transition: background-size 500ms;
  
}
#one::before,#two::before,#three::before,#four::before{
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 0;
  height:100%;
  width: 100%;
  transform-origin: bottom right;
  transform: scaleX(0);
  background-color: rgba(0, 0, 0, 0.36);
  transition: transform 300ms ease-in-out;
}
#two{
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5) 0 100%),url("https://media.architecturaldigest.com/photos/5ef5f6b4e5c8c1d259c3b00b/master/pass/GettyImages-803432314.jpg");
  background-position-x:50% ;
  background-position-y:40%;
  background-size: 250%;
}
#three{
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5) 0 100%),url("https://wallpaperaccess.com/full/150717.jpg");
  background-position-x:50% ;
  background-position-y:50%;
}

#four{
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5) 0 100%),url("https://internationalyouthclub.org/wp-content/uploads/2022/04/Ladakh-2023-36.jpg");
  background-position-x:10% ;
  background-position-y:90%;
}
#one:hover::before,#two:hover::before,#three:hover::before,#four:hover::before{
  text-align: center;
  transform: scaleX(1);
  transform-origin: bottom left;
}
#one:hover, #two:hover, #three:hover,#four:hover{
  background-size: 200%;
}

.destination-card:hover::before {
  backdrop-filter: blur(3.5px);
}

.destination-card:hover h2, .destination-card:hover h3, .destination-card:hover h5 {
  visibility: hidden;
}

#one:hover h2, #two:hover h2, #three:hover h2,#four:hover h2,
#one:hover h3, #two:hover h3, #three:hover h3,#four:hover h3,
#one:hover h5, #two:hover h5, #three:hover h5 ,#four:hover h5{
  visibility: visible;
}
#one:hover .listele2,
#two:hover .listele2,
#three:hover .listele2,
#four:hover .listele2{
  visibility: visible;
}

footer {
  position: relative;
  top: 100%;
  color: wheat;
  text-align: center;
}
#h1,#h3,#h5,#h7{
  color: #fff ;
  position: absolute;
  left: 50%;
  top:40%;
  transform: translate(-50%);
  transition: visibility 300ms;
}

.list2{
  display: inline;
  height: 250px;
  width:fit-content;
  visibility: hidden;
  margin-right: 0%;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%,-40%);
}
#package{
  position: absolute;
  top:10%;
  text-align: center;
}
#description,#details{
  display: inline;
  height: 250px;
  width: fit-content;
  visibility: hidden;
  margin-right: 0%;
  position: absolute;
  left: 50%;
  top: 95%;
  transform: translate(-50%,-40%);
  color: rgb(199, 199, 199);
  cursor: pointer;
}
#details{
  position:relative;
  top: 96%;
  left:auto;
  transform: translate(-50%,-75%);
  color: rgb(255, 68, 0);
}

#details::after{
  content: '';
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 0;
  height:2px;
  width: 100%;
  transform-origin: bottom right;
  transform: scaleX(0);
  background-color: whitesmoke  ;
  transition: transform 300ms ease-in-out;
}
#details:hover::after{
  transform: scaleX(1);
  transform-origin: bottom left;
}
.prev, .next {
  position: absolute;
  text-decoration: none;
  top: 50%;
  transform: translateY(-50%);
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 24px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
}
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}
.prev, .next {
  background-color: transparent;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  width: auto;
  margin-top: -5%;
  padding: 1%;
  padding-left: 1%;
  border: 0px;
  color: white;
  font-weight: bold;
  font-size: 24px;
  transition: 0.6s ease;
  cursor: pointer;
}.next {
  
  transform: translate(-125%,-50%);
  right: 0;
  margin-right: 0%;
  border-radius: 3px 0 0 3px;
  
}
#popular{
  font-family:  Haettenschweiler, 'Arial Narrow Bold', sans-serif;
