  justify-content: space-between;
}

.date-inputs {
  display: flex;
  justify-content: space-between;
}

.date-inputs label,
.date-inputs input {
  margin-right: 10px;
  margin-top: -10%;
}

label {
  color: #ffffff;
  margin: 15px;
