#submit{
  height: fit-content;
  width: fit-content;
  transform: translate(-75%,300%);
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
  padding: 8px;
  transition: color 500ms,background-color 500ms;
}
#submit:hover{
  background-color: rgba(255, 255, 255, 0.742);
  color: black;
  border-color: black;
}
#hotel-sec{
